// animate
@import "/scss/animate/animate.scss";

//-----------------------------------//
//------ COLORS SCHEMES ------
//-----------------------------------//
$text:#666;
$firstcolor:#0098ef;
$secondcolor:#121B22;

/*-----------------------------------*/
/*------ 1. TYPOGRAPHY ------*/
/*-----------------------------------*/
html {
	font-size: 15px;
}
body {
	font-family: 'Lato', sans-serif;
	font-size: 15px;
	font-weight: 400;
	color: $text;
	overflow-x: hidden !important;
}
p {
	line-height: 26px;
}
h1 {
	font-family: 'Montserrat', sans-serif;
	font-size: 3.5rem;
	font-weight: 800;
	margin-bottom: 2rem;
	text-transform: uppercase;
}
h2 {
	font-family: 'Montserrat', sans-serif;
	color: #222;
	font-size: 2rem;
	font-weight: 800;
	text-transform: uppercase;
}
h2 span {
	font-weight: 600;
	color: $firstcolor;
}
h3 {
	font-family: 'Montserrat', sans-serif;
	font-size: 1.5rem;
	color: #444;
	font-weight: 600;
}
h5 {
	color: #444;
}
.top-headings-section {
	padding-bottom: 2rem;
}
.top-headings-section p {
	margin-left: auto;
	margin-right: auto;
	font-weight: 400;
	color: #555;
	max-width: 550px;
}
.top-headings-section h2::after {
	display: block;
	height: 3px;
	font-weight: bold;
	background-color: $firstcolor;
	content: " ";
	width: 100px;
	margin: 0 auto;
	margin-top: .5rem;
	margin-bottom: 1.5rem
}
*::after,
*::before {
	box-sizing: border-box;
}
.section-title {
	padding-left: 35px;
	position: relative;
	margin-bottom: 2.5rem;
}
.section-title:before {
	top: 0;
	left: 0;
	bottom: 0;
	width: 15px;
	content: '';
	border-radius: 3px;
	position: absolute;
	background-color: $firstcolor;
}
.section-title h3 {
	font-size: 18px;
	color: #111;
	text-transform: uppercase;
}
.section-title h2 {
	font-size: 36px;
	color: $firstcolor;
	text-transform: uppercase;
}
.section-title.col-md-5 {
	margin-left: 1rem;
}
img {
	display: inline-block;
	max-width: 100%;
	height: auto;
}

/*-----------------------------------*/
/*------ 2. SECTION HEADINGS ------*/
/*-----------------------------------*/
.header-top {
	background: $firstcolor;
	color: #7f8997;
	display: block;
	padding: 1rem 0;
}
.header.head-tr {
	position: absolute;
	width: 100%;
	z-index: 99999;
}
.header-top.head-tr {
	background: rgba(255, 255, 255, 0.06);
	color: #7f8997;
	display: block;
	padding: 1rem 0;
}
.heading.head-tr {
	background: rgba(255, 255, 255, 0.06);
	color: #fff;
	border-top: 1px solid rgba(255, 255, 255, 0.15);
	border-bottom: 1px solid rgba(255, 255, 255, 0.15);
	position: relative;
	padding: 1rem 0;
	transition: all .5s ease;
}
@media screen and (max-width: 767px) {
	.header-top {
		padding: 0px;
	}
}
.header-top .container {
	display: flex;
	justify-content: space-between;
	margin-left: auto;
}
.top-info {
	display: flex;
}
.header-top p {
	margin-right: 2rem;
	color: #fff;
	font-size: 13px;
	font-weight: 400;
	margin-bottom: 0px;
}
.header-top p i {
	color: #fff;
	margin-right: .5rem;
	font-size: 1.3rem;
}
.top-social {
	display: flex;
}
.top-social .login-wrap ul {
	justify-content: flex-end;
	list-style: none;
	margin-bottom: 0px;
}
.top-social .login-wrap ul li {
	padding: 0px 12px;
	position: relative;
}
.top-social .login-wrap ul li a {
	color: #fff;
	text-decoration: none;
	transition: all .5s ease;
}
.top-social .login-wrap ul li a:hover {
	color: $secondcolor;
}
.top-social .login-wrap ul li a i {
	margin-right: 5px;
}
.header-top .social-icons {
	text-align: right;
	margin-left: .6rem;
}
.header-top .social-icons i {
	line-height: 26px;
	font-size: 1.2rem;
	margin-right: 1rem;
	transition: all .5s ease;
}
.social-icons-header .fa-facebook-f {
	color: #fff;
}
.social-icons-header .fa-twitter {
	color: #fff;
}
.social-icons-header .fa-google-plus-g {
	color: #fff;
}
.social-icons-header .fa-linkedin {
	color: #fff;
}
.social-icons-header .fa-skype {
	color: #fff;
}
.header-top .social-icons:hover {
	color: $firstcolor;
}
.header-top .dropdown {
	display: inline-block;
	background: transparent
}
.header-top .dropdown button {
	background: transparent none repeat scroll 0 0;
	border: medium none;
	color: #fff;
	transition: all 0.4s ease 0s
}
.header-top .dropdown button:hover {
	color: $secondcolor;
}
.header-top .dropdown .dropdown-menu {
	border-radius: 0;
	left: auto;
	margin: 0;
	min-width: 115px;
	padding: 0px;
	position: absolute;
	right: 0;
	z-index: 9999999
}
.header-top .dropdown-menu img {
	margin-right: 5px;
}
.header-top .dropdown-menu li {
	color: #333;
	cursor: pointer;
	padding: 5px 10px
}
.header-top .dropdown-menu li:hover {
	background: #eee none repeat scroll 0 0
}
.heading {
	background: $secondcolor;
	color: #fff;
	position: relative;
	padding: 1rem 0;
	transition: all .5s ease;
}
.sticky,
.sticky.head-tr {
	animation: 600ms ease-in-out 0s normal none 1 running fadeInDown;
	background: $secondcolor none repeat scroll 0 0;
	left: 0;
	margin: auto;
	position: fixed;
	top: 0;
	width: 100%;
	z-index: 99999;
}
@media screen and (max-width: 991px) {
	.heading.head-tr {
		background: rgba(255, 255, 255, 0.06);
		padding-bottom: 0px;
	}
	.header.head-tr #main-menu {
		background: #fff;
		width: 100%;
	}
	.header.head-tr #main-menu ul a,
	.header.head-tr #main-menu .po a::after {
		color: #000 !important;
	}
	.sticky.head-tr {
		background: #121B22 none repeat scroll 0 0;
	}
	.header.head-tr .heading .logo img {
		padding-left: 15px;
		padding-bottom: 1rem;
	}
	.header.head-tr .button-menu {
		padding-right: 18px !important;
	}
	.heading.head-tr .container {
		padding-left: 0px;
		padding-right: 0px;
	}
	.header.head-tr .search-button,
	.header.head-tr .button-menu {
		margin-bottom: 1rem !important;
	}
}
.heading .container {
	display: flex;
	align-items: center;
	flex-wrap: wrap;
}
.heading .logo img {
	width: 60px;
}
@media screen and (max-width: 414px) {
	.heading .logo img {
		width: 60px;
	}
}
@media screen and (max-width: 575px) {
    .header.head-tr #bloq-search {
        padding-left: 15px;
        padding-right: 15px;
    }
    .tp-caption.slid-2 {
        padding-top: 7.7rem;
    }
}
.search-button,
.button-menu {
	border: 0;
	font-size: 1.5rem;
	margin: 0;
	padding: 0 .5rem;
	background: transparent;
	color: #fff;
}
.search-button:focus,
.button-menu:focus {
	outline: 0;
}
.search-button {
	color: #fff;
	margin-left: auto;
	margin-right: .8rem;
}
.get-quote {
	background: $secondcolor;
	margin-left: 2rem;
	color: #fff;
	padding: 1rem;
	order: 4;
	border-radius: 5px;
	transition: all .5s ease;
}
.get-quote:hover {
	background: $secondcolor;
	color: #fff !important;
}
.get-quote p {
	text-transform: uppercase;
	margin: 0px;
	font-weight: 600;
}
.get-quote a {
	text-decoration: none;
	color: #fff;
}
.get-quote a:hover {
	color: #fff;
}
#bloq-search {
	flex: 1 1 100%;
}
.bloq-search {
	display: flex;
	height: 3rem;
	margin: 1.3rem 0;
	z-index: 1;
}
.bloq-search input[type="text"] {
	border: 1px solid $firstcolor;
	border-radius: 5px 0 0 5px;
	margin-bottom: 0px;
	padding: 1rem;
	flex: 1 1 auto;
}
.bloq-search input[type="submit"] {
	flex: 1 1 auto;
	border: 0;
	width: auto;
	border-radius: 0 5px 5px 0;
	background: $firstcolor;
	color: #fff;
	cursor: pointer;
	margin-bottom: 0px;
	padding: 0 1rem;
	transition: all .5s ease;
}
.bloq-search input[type="submit"]:hover {
	background: $secondcolor;
	color: #fff;
}
#main-menu {
	flex: 1 1 100%;
}
#main-menu ul {
	padding: 0;
	list-style: none;
	margin: 1rem 0 0 0;
}
#main-menu ul a {
	display: block;
	padding: .8rem 0;
	text-align: center;
	color: #fff;
	text-transform: uppercase;
	border-top: 1px solid #e5e5e5;
	text-decoration: none;
}
#main-menu ul a:hover,
#main-menu ul li.active a {
	color: $firstcolor;
}
#main-menu .card {
	background: transparent;
}
#main-menu .card a:hover {
	background: transparent;
}
#main-menu .po a::after {
	content: "\f0d7";
	font-family: FontAwesome;
	color: #fff;
	margin-left: .3rem;
}
.text-heading {
	display: flex;
	flex: 1 1 auto;
	align-items: center;
}

/*------ ARROW DOWN ------*/
.arrow-down a {
	display: inline-block;
	color: #fff;
	font-size: 3rem;
	animation: animate-arrow 2s ease-in-out infinite;
}
@keyframes animate-arrow {
	from {
		transform: translateY(0);
	}
	50% {
		transform: translateY(6px);
	}
	to {
		transform: translateY(0);
	}
}
/*------ HEADER SLIDER ------*/
.rev_slider_wrapper {
	width: 100%;
}
.slotholder {
	position: relative;
}
.slotholder:after {
	background: rgba(0, 0, 0, 0.20);
	width: 100%;
	height: 100%;
	content: "";
	position: absolute;
	left: 0;
	top: 0;
	bottom: 0;
	pointer-events: none;
}
.tp-leftarrow {
	transition: all .5s ease;
}
.tp-leftarrow:hover {
	background: $firstcolor;
}
.tp-rightarrow {
	transition: all .5s ease;
}
.tp-rightarrow:hover {
	background: $firstcolor;
}
.tp-parallax-wrap {
	top: 10px;
}
.tp-bannertimer {
	z-index: 21;
}
.mfp-close-btn-in .mfp-close {
	color: #fff;
	font-size: 48px;
}
.mfp-bg {
	z-index: 1112;
}
.mfp-wrap {
	z-index: 1113;
}
.rev_slider a {
	box-shadow: none;
}
.text-theme-colored2 {
	color: #0098ef !important;
}
.tp-caption.font-p {
	font-family: 'Lato', sans-serif;
	font-size: 16px;
	font-weight: 400;
}
@media screen and(max-width: 496px) {
	.tp-caption.font-p {
		margin-top: .8rem;
	}
}
.btn-default.btn-theme-colored2 {
	color: #fff;
	font-weight: 600;
	background-color: $secondcolor;
	border-color: #fff;
	font-size: 16px;
	padding: 13px 32px;
	margin-top: .8rem;
	margin-right: 1rem;
	line-height: 1.3;
	transition: all .5s ease;
}
.btn-default.btn-theme-colored2:hover {
	background: $firstcolor;
	color: #fff;
	border-color: #fff;
}
.btn-dark.btn-theme-colored {
	color: #fff;
	background-color: $firstcolor;
	border-color: #fff;
	font-size: 16px;
	font-weight: 600;
	padding: 13px 32px;
	margin-top: .8rem;
	line-height: 1.3;
	transition: all .5s ease;
}
.btn-dark.btn-theme-colored:hover {
	background: $secondcolor;
	color: #fff;
	border-color: #fff;
}

/*------ BUTTOM HOVER EFFECT ------*/
/* Box colors */
.bg-1 {
	background: $firstcolor;
	color: #fff;
	display: inline-block;
	justify-content: flex-start;
	margin: 0px;
}
.bg-2 {
	background: #fff;
	color: #000;
	margin-top: 2rem;
	display: flex;
	justify-content: flex-start;
}
.bg-3 {
	background: transparent;
	color: #e54242;
	display: flex;
	justify-content: center;
	margin: 0px;
}
.bg-4 {
	background: transparent;
	color: #fff;
	margin-top: 2rem;
	display: flex;
	justify-content: center;
}
/* Common button styles */
.box .button {
	float: left;
	min-width: 150px;
	max-width: 250px;
	display: block;
	margin-bottom: 0px;
	padding: 1em 2em;
	border: 2px solid;
	background: none;
	color: inherit;
	vertical-align: middle;
	position: relative;
	z-index: 1;
	-webkit-backface-visibility: hidden;
	-moz-osx-font-smoothing: grayscale;
}
.button:focus {
	outline: none;
}
.button > span {
	vertical-align: middle;
}
/* Text color adjustments */
.bg-1 .button {
	color: #fff;
	border-color: #fff;
}
.bg-2 .button {
	color: #000;
	border-color: $firstcolor;
}
.bg-4 .button {
	color: #fff;
	border-color: transparent;
}
.bg-3 .button {
	color: $firstcolor;
	border-color: $firstcolor;
}
.bg-3 .button:hover {
	border-color: $firstcolor;
	color: #fff;
}
@media screen and (max-width: 768px) {
	.bg-3 {
		padding-top: 1rem;
	}
}
/* Sizes */
.button--size-s {
	font-size: 15px;
	font-weight: bold;
}
.button--size-m {
	font-size: 16px;
}
.button--size-l {
	font-size: 18px;
}
/* Typography and Roundedness */
.button--text-upper {
	letter-spacing: 2px;
	text-transform: uppercase;
}
.button--text-thin {
	font-weight: 300;
}
.button--text-medium {
	font-weight: 500;
}
.button--text-thick {
	font-weight: 600;
}
.button--round-s {
	border-radius: 5px;
}
.button--round-m {
	border-radius: 15px;
}
.button--round-l {
	border-radius: 40px;
}
/* Borders */
.button--border-thin {
	border: 1px solid;
}
.button--border-medium {
	border: 2px solid;
}
.button--border-thick {
	border: 3px solid;
}
/* Wayra */
.button--wayra {
	overflow: hidden;
	cursor: pointer;
	width: 210px;
	transition: border-color 0.3s, color 0.3s;
	transition-timing-function: cubic-bezier(0.2, 1, 0.3, 1);
}
.button--wayra::before {
	content: '';
	position: absolute;
	top: 0;
	left: 0;
	width: 150%;
	height: 100%;
	background: $firstcolor;
	z-index: -1;
	transform: rotate3d(0, 0, 1, -45deg) translate3d(0, -3em, 0);
	transform-origin: 0% 100%;
	transition: transform 0.3s, opacity 0.3s, background-color 0.3s;
}
.button--wayra:hover {
	color: #fff;
	border-color: #fff;
}
.button--wayra.button--inverted:hover {
	color: #fff;
	border-color: $firstcolor;
}
.button--wayra:hover::before {
	opacity: 1;
	background-color: $firstcolor;
	transform: rotate3d(0, 0, 1, 0deg);
	transition-timing-function: cubic-bezier(0.2, 1, 0.3, 1);
}
.button--wayra.button--inverted:hover::before {
	background-color: $firstcolor;
}
.portfolio .box a {
	background: $secondcolor;
	text-decoration: none;
	text-align: center;
}
/* Moema */
.button--moema {
	padding: 1.5em 3em;
	border-radius: 50px;
	background: #7986cb;
	color: #fff;
	transition: background-color 0.3s, color 0.3s;
}
.button--moema.button--inverted {
	background: #ECEFF1;
	color: #37474f;
}
.button--moema::before {
	content: '';
	position: absolute;
	top: -20px;
	left: -20px;
	bottom: -20px;
	right: -20px;
	background: inherit;
	border-radius: 50px;
	z-index: -1;
	opacity: 0.4;
	transform: scale3d(0.8, 0.5, 1);
}
.button--moema:hover {
	transition: background-color 0.1s 0.3s, color 0.1s 0.3s;
	color: #ECEFF1;
	background-color: #3f51b5;
	animation: anim-moema-1 0.3s forwards;
}
.button--moema.button--inverted:hover {
	color: #ECEFF1;
	background-color: #7986cb;
}
.button--moema:hover::before {
	animation: anim-moema-2 0.3s 0.3s forwards;
}
@-webkit-keyframes anim-moema-1 {
	60% {
		transform: scale3d(0.8, 0.8, 1);
	}
	85% {
		transform: scale3d(1.1, 1.1, 1);
	}
	100% {
		transform: scale3d(1, 1, 1);
	}
}
@keyframes anim-moema-1 {
	60% {
		transform: scale3d(0.8, 0.8, 1);
	}
	85% {
		transform: scale3d(1.1, 1.1, 1);
	}
	100% {
		transform: scale3d(1, 1, 1);
	}
}
@-webkit-keyframes anim-moema-2 {
	to {
		opacity: 0;
		transform: scale3d(1, 1, 1);
	}
}
@keyframes anim-moema-2 {
	to {
		opacity: 0;
		transform: scale3d(1, 1, 1);
	}
}

