	transform: translate(-50%, -50%);
	width: 352px;
	width: 22rem;
	z-index: 2;
}
.welcome .wprt-image-video .iq-waves .waves {
	animation: 3s ease-in-out 0s normal none infinite running waves;
	background: rgb(0, 152, 239) none repeat scroll 0 0 padding-box;
	border-radius: 320px;
	height: 352px;
	height: 22rem;
	opacity: 0;
