/*------ HOVER EFFECT ------*/
.hover-effect figure {
	position: relative;
	overflow: hidden;
	margin: 0;
}
.hover-effect figure::before {
	position: absolute;
	top: 0;
	left: -90%;
	z-index: 2;
	display: block;
	content: '';
	width: 50%;
	height: 100%;
	background: linear-gradient(to right, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, .3) 100%);
	transform: skewX(-25deg);
}
.hover-effect:hover figure::before {
	animation: shine .75s;
}
@-webkit-keyframes shine {
	100% {
		left: 125%;
	}
}
@keyframes shine {
	100% {
		left: 125%;
	}
}
.hover-effect figure img {
	transform: scale(1);
	transition: .5s ease;
}
.hover-effect:hover figure img {
	transform: scale(1.3);
}
